export default class Tabs {
    constructor( options ) {
        this._tabsLabelsSelector = options.tabsLabels;
        this._tabsContentsSelector = options.tabsContents;
        this.onChange = options.onChange;
        
        this.prepareData(this._tabsLabelsSelector, this._tabsContentsSelector);
        this.attachEvents();
    }

    prepareData(tabsLabelsSelector, tabsContentsSelector) {
        const self = this;
        let $tabsLabels = $(tabsLabelsSelector),
            $tabsContents = $(tabsContentsSelector);
        
        this._tabsLabels = {};
        this._activeTabsLabels = {};
        $tabsLabels.each(function(i) {
            let $this = $(this),
                tabGroup = $this.data('tabGroup') || 'default';
            
            if (!self._tabsLabels[tabGroup]) {
                self._tabsLabels[tabGroup] = $([]);
            }

            self._tabsLabels[tabGroup] = self._tabsLabels[tabGroup].add(this);

            if ($this.hasClass('active')) {
                if (self._activeTabsLabels[tabGroup]) {
                    self._activeTabsLabels[tabGroup].removeClass('active');
                }

                $this.addClass("active");
                self._activeTabsLabels[tabGroup] = $this;
            }

            if (!self._activeTabsLabels[tabGroup] && ((i + 1) === $tabsLabels.length)) {
                $this.addClass("active");
                self._activeTabsLabels[tabGroup] = $this;
            }
        });


        this._tabsContents = {};
        this._activeTabsContents = {};
        $tabsContents.each(function(i) {
            let $this = $(this),
                tabGroup = $this.data('tabGroup') || 'default';
            
            if (!self._tabsContents[tabGroup]) {
                self._tabsContents[tabGroup] = $([]);
            }

            self._tabsContents[tabGroup] = self._tabsContents[tabGroup].add(this);

            if ($this.hasClass('active')) {
                if (self._activeTabsContents[tabGroup]) {
                    self._activeTabsContents[tabGroup].removeClass('active');
                }
                
                $this.addClass("active");
                self._activeTabsContents[tabGroup] = $this;
            }

            if (!self._activeTabsContents[tabGroup] && ((i + 1) === $tabsContents.length)) {
                $this.addClass("active");
                self._activeTabsContents[tabGroup] = $this;
            }
        });
    }
    
    attachEvents() {
        const self = this;

        $(this._tabsLabelsSelector).on("click", function() {
            let $this = $(this);
            self.activate( $this.data('tab'), $this.data('tabGroup') );
            return false;
        });
    }

    deactivateActive(tabGroup = 'default') {
        this._activeTabsLabels[tabGroup].removeClass("active");
        this._activeTabsContents[tabGroup].removeClass("active");
    }

    activate( tabIndex, tabGroup = 'default', deactivateActive = true ) {
        if ( deactivateActive ) {
            this.deactivateActive(tabGroup);
        }

        this._activeTabsLabels[tabGroup] = this._tabsLabels[tabGroup].filter(`[data-tab="${tabIndex}"]`);
        this._activeTabsContents[tabGroup] = this._tabsContents[tabGroup].filter(`[data-tab="${tabIndex}"]`);

        this._activeTabsLabels[tabGroup].addClass("active");
        this._activeTabsContents[tabGroup].addClass("active");

        this.onChange(tabIndex);
    }
}


